@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Encode Sans Expanded', sans-serif;
}

body {
	font-family: "Lato", sans-serif;
	box-sizing: border-box;
	padding-top: 75px;
	margin: 0;
	box-sizing: border-box;
	padding: 75px 0 0 0;

}

h2 {
	padding: 20px;
	box-sizing: border-box;
	max-width: 1500px;
	margin: auto;
	display: inline-block;
	width: 100%;
	text-align: left;
}

h1 {
	text-align: left;
}

p {
	text-align: left;
}

header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 75px;
	background: #000000;
	z-index: 10;
}

header h1 {
	margin: 0;
}

.shields {
	padding: 20px;
	box-sizing: border-box;
	max-width: 1500px;
	margin: auto;
}

button {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	-webkit-filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
	        filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
	transition: background .5s ease, color .5s ease;
	border: none;
	background: #ffd800;
	border-radius: 30px;
	text-transform: uppercase;
	box-sizing: border-box;
	padding: 15px 40px;
	font-weight: 400;
	font-size: 13px;
	cursor: pointer;

	&:hover {
		color: #ffffff;
		background: #222222;
		-webkit-filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
		        filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
	}
}

header section {
	max-width: 1500px;
	height: 35px;
	margin: auto;
	padding: 20px;
	background: #ffd800;
}

header section a {
	float: right;
	padding: 3px 0;
}

:global(.center) {
	text-align: center;
	align-content: center;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

:global(.slider-content) {
	color: white;

	&:before {
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
		background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
		bottom: 0;
		left: 0;
	}
}

.page {
	width: 100%;
	max-width: 1000px;
	margin: auto;
	margin-bottom: 100px;
}

.slider {
	position: relative;
	width: 100%;
	height: 80vh;
	overflow: hidden
}

.slider a.previousButton, .slider a.nextButton {
	font-size: 22px;
	line-height: 0;
	display: block;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	transition: all .3s linear;
	z-index: 1;
	color: #333;
	padding: 10px;
	text-decoration: none;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden
	/* prevent jump effect when scaling */
}

.slider a.previousButton:not(.disabled):hover, .slider a.nextButton:not(.disabled):hover {
	transform: translateY(-50%) scale(1.25);
	cursor: pointer;
}

.slider a.previousButton {
	left: 20px;
}

.slider a.nextButton {
	right: 20px;
}

.slide {
	width: 100%;
	height: 100%;
	position: absolute;
	overflow: hidden
}

.slide.hidden {
	visibility: hidden;
}

.slide.previous {
	left: -100%;
}

.slide.current {
	left: 0;
}

.slide.next {
	left: 100%;
}

.slide.animateIn,
	.slide.animateOut {
	transition: all 2s ease;
}

.slide.animateIn.previous,
		.slide.animateIn.next {
	left: 0;
	visibility: visible;
}

.slide.animateOut.previous {
	left: 100%;
}

.slide.animateOut.next {
	left: -100%;
}


